@import url('https://fonts.googleapis.com/css2?family=Karla:wght@400;700&display=swap');

body {
  margin: 0;
  font-family: 'Karla', Arial, Helvetica, sans-serif;
  background-color: #F5F7FB;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

main {
  width: 90%;
  padding: 50px;
}

.yellowBlob {
  position: absolute;
  top: 0;
  right: 0;
}

.blueBlob {
  position: absolute;
  bottom: 0;
  left: 0;
}

.start--heading {
  width: 412px;
  height: 41px;
  font-family: 'Karla';
  font-style: normal;
  font-weight: 700;
  font-size: 31.25px;
  line-height: 37px;
  text-align: center;

  color: #293264;

}

.start--btn {
  width: 193px;
  height: 52px;
  font-family: 'Karla';
  background: #4D5B9E;
  border-radius: 15px;
  border: none;
  color: white;
  font-style: normal;
  font-weight: 500;
  font-size: 16px;
  line-height: 19px;
  text-align: center;

  color: #F5F7FB;
}

.check--btn {
  width: 193px;
  height: 52px;
  font-family: 'Karla';
  background: #4D5B9E;
  border-radius: 15px;
  border: none;
  font-style: normal;
  font-weight: 500;
  font-size: 16px;
  line-height: 19px;
  text-align: center;
  margin: auto;
  position: absolute;
  left: 50%;
  color: #F5F7FB;
}

.start {
  width: 100%;
  height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.form--heading {
  font-family: 'Karla';
  font-style: normal;
  font-weight: 700;
  font-size: 20px;
}

h3 {

  float: left;
  margin-left: 10%;
  margin-right: 0px;
  margin-bottom: 0px;
  font-style: normal;
  font-weight: 700;
  font-size: 18.8px;
  line-height: 15px;
  text-align: center;

  color: #293264;
}

.right {
  background-color: #94D7A2;
}

.answer-btn {
  font-family: 'Karla';
  font-style: normal;
  font-weight: 500;
  font-size: 12.24px;
  line-height: 18px;
  padding: 10px 15px;
  margin: 0 10px 15px 0;
  border: 0.794239px solid #4D5B9E;
  border-radius: 7.94239px;
  background-color: white;
  color: #293264;
  cursor: pointer;
  transition: background-color 0.2s, opacity 0.2s;
}

.answer-btn:hover {
  background-color: #DEE2FF;
}

.answer-btn.selected {
  background-color: #D6DBF5;
}

.answer-btn.correct {
  background-color: #94D7A2;
  border-color: #94D7A2;
  color: #293264;
}

.answer-btn.incorrect {
  background-color: #F8BCBC;
  border-color: #F8BCBC;
  color: #293264;
}

.answer-btn.faded {
  opacity: 0.6;
}

.answers {
  display: flex;
  flex-wrap: wrap;
  gap: 10px;
  margin: 20px 0;
}